<template>
	<view class="content">
		<view class="title-box">
			<view class="info">
				<view class="image-box">
					<image
						style="width:124rpx;
					           height:124rpx;
							   border-radius: 50%;"
						src="https://img.yzcdn.cn/vant/cat.jpeg"
					/>
				</view>
				<view class="text-b">
					<div style="margin-top:20rpx">{{user.desc}}</div>
					<!-- <div style="font-size: 28rpx;"></div> -->
				</view>
			</view>
		</view>
		<view class="card">
			<uni-grid :column="3" :showBorder="false" @change="changeMenu">
				<uni-grid-item v-for="(item, index) in menuList" :index="index" :key="index" v-show="item.show" style="text-align: center;">
					<image class="icon" :src="item.image"></image>
					<text class="text">{{ item.name }}</text>
				</uni-grid-item>
			</uni-grid>
		</view>
		<!-- <view class="btn-box"><button type="primary" style="font-size:32rpx" @click="logout">退出登录</button></view> -->
	</view>
</template>

<script>
export default {
	data() {
		return {
			user: {
				username: uni.getStorageSync('username'),
				desc: uni.getStorageSync('deptName')
			},
			menuList: [
				{
					image: '/static/img/index/icon1.png',
					name: '巡检任务',
					show: true
				},
				{
					image: '/static/img/index/icon2.png',
					name: '隐患上报',
					show: uni.getStorageSync('roleId')==8
				}
			]
		};
	},
	methods: {
		logout() {
			uni.navigateTo({
				url: '../login/index'
			});
		},
		changeMenu(e){
			switch(e.detail.index){
				case 0:
				uni.navigateTo({
					url: '../patrolTask/index'
				});
				break;
				case 1:
				uni.navigateTo({
					url: `../patrolTask/troubleList`
				});
				break;
			}
		},
	}
};
</script>

<style scoped>
.title-box {
	height: 300rpx;
	background: linear-gradient(180deg, #265ad2, #4695f9);
	padding: 74rpx 72rpx 0;
}

.info {
	height: 140rpx;
	display: flex;
}

.image-box {
	background-color: #4b7ce0;
	padding: 8rpx;
	border-radius: 50%;
	text-align: center;
}

.text-b {
	line-height: 56rpx;
	padding-top: 16rpx;
	margin-left: 16rpx;
	color: #fff;
	font-size: 36rpx;
}

.card {
	/* margin: -100rpx 40rpx; */
	position: absolute;
	top: 260rpx;
	left: 40rpx;
	right: 40rpx;
	background-color: #fff;
	border-radius: 16rpx;
	padding: 30rpx;
	box-shadow: 0px 8px 64px 0px rgba(63, 63, 63, 0.07);
}

.icon {  
	width: 72rpx;
	height: 72rpx;
	margin:40rpx auto 20rpx;
	 
}

.text {
	font-size: 28rpx;
	font-family: Source Han Sans CN;
	color: #333333;
}

.btn-box {
	position: absolute;
	bottom: 60rpx;
	left: 40rpx;
	right: 40rpx;
}
</style>
